.pt-80 {
	padding-top: 50px;
}
.pb-80 {
	padding-bottom: 50px;
}
@include breakpoint(md) {
	.pt-80 {
		padding-top: 80px;
	}
	.pb-80 {
		padding-bottom: 80px;
	}
}
.section-header {
	max-width: 500px;
	margin: 0 auto 35px;
	.title {
		font-size: 26px;
		margin-bottom: 12px;
	}
	@include breakpoint(sm) {
		.title {
			font-size: 30px;
			margin-bottom: 18px;
		}
	}
	@include breakpoint(md) {
		margin-bottom: 49px;
	}
	@include breakpoint(xxl) {
		max-width: calc(500px + 5vw);
		.title {
			font-size: calc(30px + 0.25vw);
		}
	}
}
.text-base {
	color: var(--base-1);
}
.about-img {
	width: 100%;
	max-width: 466px;
}
.about-content {
	.title {
		font-size: 26px;
		margin-bottom: 12px;
	}
	.subtitle {
		font-size: 22px;
		font-weight: 500;
		margin-bottom: 15px;
	}
	.txt {
		margin-bottom: 20px;
	}
	@include breakpoint(sm) {
		.title {
			font-size: 34px;
		}
		.subtitle {
			font-size: 28px;
			margin-bottom: 25px;
		}
		.txt {
			margin-bottom: 30px;
		}
	}
	max-width: 566px;
	// margin-inline-start: auto;
	// margin-inline-end: auto;
	@include breakpoint(xxl) {
		// max-width: calc(500px + 5vw);
		.title {
			font-size: calc(30px + 0.25vw);
		}
		.txt {
			margin-bottom: 70px;
		}
	}
}
