.feature-item {
	padding: 70px 18px 63px;
	text-align: center;
	&-icon {
		img {
			width: 62px;
			height: 72px;
			object-fit: contain;
		}
		margin-bottom: 22px;
	}
	.title {
		margin-bottom: 30px;
		color: var(--white);
	}
	.txt {
		max-width: 244px;
		margin: 0 auto;
	}
	font-size: 13px;
	line-height: 20px;
	color: var(--white);
	box-shadow: 0px 0px 70px rgba(21, 21, 20, 0.1);
	* {
		position: relative;
		z-index: 2;
	}
	position: relative;
	&::before,
	&::after {
		background: linear-gradient(
			140.44deg,
			var(--base-2) -2.03%,
			var(--base-1) 98.87%
		);
		content: "";
		position: absolute;
		inset: 0;
		border-radius: 5px;
		transition: all ease 0.3s;
	}
	&::before {
		inset: 2px;
		background: linear-gradient(
			140.44deg,
			var(--base-2) -10.03%,
			var(--base-1) 48.87%
		);
		z-index: 1;
	}
	&:hover {
		&::before {
			opacity: 0;
		}
	}
	@include breakpoint(max-xl) {
		// @include breakpoint(sm) {
		padding: 50px 10px;
		.feature-item-icon {
			img {
				height: 50px;
			}
			margin-bottom: 12px;
		}
		.title {
			margin-bottom: 20px;
		}
	}
	// }
}
.feature-slider {
	.owl-stage-outer {
		overflow: visible;
	}
}

.choose-item {
	position: relative;
	&-content {
		position: absolute;
		inset: 0;
		z-index: 1;
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		transition: all ease 0.3s;
		&::before {
			background: linear-gradient(
				90deg,
				rgba(var(--base-15-rgb), 0.92) 0%,
				rgba(0, 0, 0, 0.38) 100%
			);
			inset: 0;
			content: "";
			position: absolute;
			transition: all ease 0.3s;
		}
		.cont {
			margin-inline-start: 5%;
			width: 90%;
			transition: all ease 0.3s;
			position: relative;
			.title {
				font-size: 31px;
				font-weight: 500;
				line-height: 37px;
				color: var(--white);
				@include breakpoint(max-lg) {
					font-weight: 400;
				}
			}
		}
	}
	img {
		width: 100%;
		position: absolute;
		width: 100%;
		height: 100%;
		object-fit: cover;
		transition: all ease 0.3s;
		transform: scale(1.01);
	}
	height: 216px;
	overflow: hidden;
	&:hover {
		img {
			transform: scale(1.03);
		}
	}
	&:not(:hover) {
		.choose-item-content {
			opacity: 0;
			.cont {
				transform: translateX(-10px) scale(0.97);
			}
		}
	}
}
.choose-wrapper {
	display: flex;
	flex-wrap: wrap;
	gap: 30px;
	@include breakpoint(max-xl) {
		gap: 24px;
	}
	@include breakpoint(max-lg) {
		gap: 16px;
	}
	.choose-item {
		&:nth-of-type(4n + 1),
		&:nth-of-type(4n + 4) {
			width: calc(710px / 1.2);
			flex-grow: 1;
		}
		&:nth-of-type(4n + 2),
		&:nth-of-type(4n + 3) {
			width: calc(455px / 1.2);
			flex-grow: 1;
			.choose-item-content {
				.cont {
					.title {
						font-size: 24px;
						line-height: 1.3;
					}
				}
			}
		}
		@include breakpoint(max-xl) {
			&:nth-of-type(4n + 1),
			&:nth-of-type(4n + 4) {
				width: calc(710px / 1.5);
				flex-grow: 1;
			}
			&:nth-of-type(4n + 2),
			&:nth-of-type(4n + 3) {
				width: calc(455px / 1.5);
				flex-grow: 1;
				.choose-item-content {
					.cont {
						.title {
							font-size: 24px;
							line-height: 1.5;
						}
					}
				}
			}
		}
		@include breakpoint(max-lg) {
			height: 200px;
			&:nth-of-type(4n + 1),
			&:nth-of-type(4n + 4) {
				width: calc(710px / 2);
			}
			&:nth-of-type(4n + 2),
			&:nth-of-type(4n + 3) {
				width: calc(455px / 2);
			}
			.choose-item-content {
				.cont {
					margin-inline-start: 3%;
					width: 94%;
					.title {
						font-size: 20px;
						line-height: 1.5;
					}
				}
			}
		}
		@include breakpoint(max-md) {
			height: 200px;
			&:nth-of-type(4n + 1),
			&:nth-of-type(4n + 4) {
				width: calc(710px / 2);
			}
			&:nth-of-type(4n + 2),
			&:nth-of-type(4n + 3) {
				width: calc(455px / 2);
			}
			.choose-item-content {
				.cont {
					margin-inline-start: 15px;
					width: calc(100% - 30px);
					.title {
						font-size: 20px;
						line-height: 1.5;
					}
				}
			}
		}
	}
}
.nav--tabs {
	border: none;
	gap: 30px;
	justify-content: space-between;
	margin-bottom: 35px;
	@include breakpoint(md) {
		margin-bottom: 45px;
	}
	li {
		.nav-link {
			white-space: nowrap;
			border: none;
			background: transparent !important;
			display: flex;
			align-items: center;
			padding: 10px 0;
			position: relative;
			&::before,
			&::after {
				content: "";
				bottom: 0;
				height: 1px;
				position: absolute;
				background: var(--border);
				transition: all ease 0.3s;
				right: 0;
				left: 0;
			}
			&::before {
				background: linear-gradient(
					140.44deg,
					var(--base-2) -2.03%,
					var(--base-1) 98.87%
				);
				z-index: 1;
				transform: scaleX(0);
			}
			img {
				margin-inline-end: 30px;
				height: 38px;
				width: 34px;
				object-fit: contain;
			}
			&.active {
				font-weight: 700;
				color: var(--base-1);
				&::before {
					transform: scaleX(1);
				}
			}
		}
	}
	@include breakpoint(max-xl) {
		gap: 24px;
		justify-content: space-evenly;
		li {
			.nav-link {
				font-size: 14px;
				padding: 7px 0;
				img {
					width: 28px;
					height: 34px;
					margin-inline-end: 15px;
				}
				&.active {
					font-weight: 500;
				}
			}
		}
	}
}
