.cta-btn-container {
	gap: 22px;
}
.cta-section {
	background: rgba(var(--base-8-rgb), 0.08);
	@include breakpoint(max-xl) {
		@include breakpoint(lg) {
			.container {
				max-width: 1133px;
			}
		}
	}
}
.cta-content {
	padding: 60px 0;
	.section-header {
		.title {
			@include breakpoint(md) {
				font-size: 28px;
			}
		}
		@include breakpoint(max-lg) {
			margin-bottom: 25px;
		}
	}
	@include breakpoint(xl) {
		.btn-base {
			padding: 15px 24px;
		}
	}
	@include breakpoint(max-md) {
