	}
}
.cta-img {
	@include breakpoint(max-xl) {
		width: 100%;
	}
	max-width: 513px;
}
.testimonial-slider {
	max-width: 900px;
	margin: 0 auto;
}
.testimonial-item {
	text-align: center;
	.img {
		cursor: pointer;
		max-width: 180px;
		margin: 0 auto;
		border-radius: 50%;
		aspect-ratio: 1;
		padding: 4px;
		background: var(--white);
		box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
		&.trns {
			transition: all ease 0.6s;
		}
		img {
			width: 100%;
			border-radius: 50%;
			aspect-ratio: 1;
		}
	}
	.quote {
		color: var(--base-1);
		font-style: italic;
		font-weight: 500;
	}
	.name {
		margin-top: 34px;
		font-size: 22px;
	}
	.designation {
		font-size: 16px;
	}
	.slide-item {
		max-width: 572px;
		margin: 0 auto;
	}
	@include breakpoint(max-sm) {
		.quote {
			font-weight: 400;
		}
		.name {
			margin-top: 10px;
			font-size: 20px;
		}
		.designation {
			font-size: 14px;
		}
	}
}
.owl-item {
	&.center {
		.img {
			background: linear-gradient(
				140.44deg,
				var(--base-2) -2.03%,
				var(--base-1) 98.87%
			);
		}
	}
	&.active + &.active:not(.center) {
		.img {
			transform: scale(0.4) translateX(-25px);
		}
	}
	&.active.center + &.active + &.active {
		.img {
			transform: scale(0.25) translateX(-15px);
			opacity: 0.3;
		}
	}
	&.active:not(.center) {
		.img {
			transform: scale(0.25) translateX(15px);
			opacity: 0.3;
			@include breakpoint(max-md) {
				transform: scale(0.45) translateX(15px) !important;
				opacity: 0.6 !important;
			}
		}
	}
	&.active.center + &.active {
		.img {
			@include breakpoint(max-md) {
				transform: scale(0.45) translateX(-15px) !important;
				opacity: 0.6 !important;
			}
		}
	}
}
.client-nav {
	position: absolute;
	bottom: calc(50% - 15px);
	&.client-prev {
		left: 0;
	}
	&.client-next {
		right: 0;
	}
	z-index: 9;
	cursor: pointer;
}
.h-148px {
	height: 148px;
	@include breakpoint(max-lg) {
		height: 125px;
	}
	@include breakpoint(max-sm) {
		height: 90px;
	}
}
.section-wrapper {
	backdrop-filter: drop-shadow(4px 4px 10px #000);
	&-inner {
		padding: 41px 80px 138px;
		background: var(--white);
		mask: url(./img/contact-bg.png) no-repeat bottom center / cover;
		-webkit-mask: url(./img/contact-bg.png) no-repeat bottom center / cover;
		border-top-left-radius: 30px;
		border-top-right-radius: 30px;
		@include breakpoint(max-xl) {
			padding-inline-start: 30px;
			padding-inline-end: 30px;
			padding-bottom: 80px;
		}
		@media (max-width: 500px) {
			padding-inline-start: 15px;
			padding-inline-end: 15px;
		}
	}
	@include breakpoint(xl) {
		margin-bottom: -10px;
	}
}
.contact-item {
	&-icon {
		position: relative;
		width: 43px;
		height: 48px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 5px;
		&::after,
		&::before {
			content: "";
			position: absolute;
			border-radius: 5px;
		}
		&::after {
			inset: 0;
			background: linear-gradient(
				140.44deg,
				var(--base-2) -2.03%,
				var(--base-1) 98.87%
			);
		}
		&::before {
			inset: 1px;
			background: linear-gradient(
				140.44deg,
				var(--base-1) -2.03%,
				var(--base-2) 98.87%
			);
			z-index: 1;
		}
		svg,
		i {
			position: relative;
			z-index: 1;
		}
	}
	.content {
		padding-inline-start: 17px;
		width: calc(100% - 43px);
		.name {
			font-size: 18px;
			font-weight: 700;
		}
		.info {
			font-size: 18px;
			color: var(--base-1);
			text-decoration: none;
			display: block;
			white-space: pre;
			word-break: break-all;
			word-wrap: break-word;
		}
	}
	display: flex;
	@include breakpoint(max-xl) {
		@include breakpoint(lg) {
			.content {
				.info {
					font-size: 16px;
				}
			}
		}
	}
	@include breakpoint(max-md) {
		.content {
			.info {
				font-size: 16px;
			}
		}
	}
}
.form--control {
	height: 50px;
	border: 1px solid var(--border);
	box-shadow: none !important;
	padding-inline-start: 14px;
}
textarea.form--control {
	height: 145px;
	padding-top: 20px;
}
.about--content {
	font-size: 16px;
	p:not(:last-child) {
		margin-bottom: 25px;
		@include breakpoint(xl) {
			margin-bottom: 30px;
		}
	}
}

#landing-loader {
	background: var(--white);
	position: fixed;
	top: 0;
	left: 0;
	z-index: 99999;
	width: 100%;
	height: 100%;
}
.banner-section,
.about--section {
	.wow {
		animation-delay: 0.6s;
		-webkit-animation-delay: 0.6s;
		-moz-animation-delay: 0.6s;
	}
}
